import React from 'react'
import styled from 'styled-components'
import { BrowserRouter, Route , Routes , Link } from "react-router-dom";

const Container=styled.div`
flex: 1;
margin: 3px;
height: 70vh;
position: relative;
`
const Image=styled.img`
width: 100%;
height: 100%;
object-fit: cover;
`
const Info=styled.div`
position: absolute;
top: 0;
left: 0;
height: 100%;
width: 100%;
display: flex;
align-items: center;
justify-content: center;
flex-direction: column;
&:hover{
  background-color: purple;
  opacity: 0.75;
  transition: 0.3s ease-in 0.2s ;
}
`
const Title=styled.h1`
color: white;
margin: 20px;


`
const Button=styled.button`
padding: 10px;

background-color: #fd88fd;
  color: white;
border: none;
font-weight: bolder;

&:hover{
  border: 2px solid #fd88fd;
  color: #fd88fd;
background-color: white;
  cursor: pointer;
}
`
export default function CatagoryItem({item}) {
  return (
    <Container>
      <Image src={item.img} />
      <Info>
          <Title>{item.title}</Title>
          <Link to="/productList"><Button>SHOP NOW</Button></Link>
      </Info>
    </Container>
  )
}

